<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right pt-3">
      <button class="btn btn-primary mt-3 text-white" @click="openModal(true)">
        <i class="fa fa-plus" aria-hidden="true"></i> 
      </button>
    </div>
    <table class="table table-hover mt-4 bg-light shadow-sm rounded">
      <thead class="thead-dark">
        <tr>
          <th width="60"></th>
          <th width="120"></th>
          <th width="250"></th>
          <th width="120" class="text-right"></th>
          <th width="120" class="text-right"></th>
          <th width="120"></th>
          <th width="120"> / </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td><img :src="item.imageUrl" width="50" alt="" /></td>
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td class="text-right">{{ item.price | currency }}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"
              ><i class="fa fa-check" aria-hidden="true"></i> </span
            >
            <span class="text-danger" v-if="!item.is_enabled"
              ><i class="fa fa-times" aria-hidden="true"></i> </span
            >
          </td>
          <td>
            <div class="btn-group" role="group">
              <button class="btn btn-outline-dark btn-sm" @click="openModal(false, item)">
                
              </button>
              <button class="btn btn-outline-danger btn-sm" @click="openModal(false, item, true)">
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- Pagination -->
    <Pagination :pagination="pagination" @gopage="getProducts"></Pagination>

    <!-- Product Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile"
                    > 
                    <i class="fa fa-spinner fa-spin fa-fw" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile" />
                </div>
                <img img="" class="img-fluid" :src="tempProduct.imageUrl" alt="" />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-dark" data-dismiss="modal"></button>
            <button type="button" class="btn btn-secondary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- Delete Modal -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="delProductModal"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-dark" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delProduct(tempProduct.id)">
              <i class="fa fa-trash-o fa-lg"></i> 
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'; // jQuery $
import Pagination from '@/components/Pagination';

export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false,
      },
      pagination: {},
    };
  },

  methods: {
    // 
    getProducts(page = 1) {
      //  ES6  1
      const vm = this;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },

    //  Modal
    openModal(isNew, item, isDel) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
        $('#productModal').modal('show');
      } else if (!isNew && isDel) {
        this.tempProduct = Object.assign({}, item); // item  tempProduct 
        $('#delProductModal').modal('show');
      } else {
        this.tempProduct = Object.assign({}, item); // item  tempProduct 
        this.isNew = false;
        $('#productModal').modal('show');
      }
    },

    //  Products 
    updateProduct() {
      const vm = this;
      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      let httpMethod = 'post';
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          $('#productModal').modal('hide'); // 
          vm.getProducts(); // 
          vm.$bus.$emit('message:push', '', 'success'); // emit
        } else {
          vm.$bus.$emit('message:push', '', 'danger');
          $('#productModal').modal('hide');
          console.log('');
        }
        // vm.products = response.data.products;
      });
    },

    // 
    delProduct(id) {
      const vm = this;
      vm.isLoading = true;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${id}`;
      this.$http.delete(api).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        $('#delProductModal').modal('hide');
        vm.getProducts(); // 
        vm.$bus.$emit('message:push', '', 'danger');
      });
    },

    // 
    uploadFile() {
      // console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
      vm.status.fileUploading = true;
      this.$http
        .post(api, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then(response => {
          // console.log(response.data);
          vm.status.fileUploading = false;
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            //  imageUrl  getter  setter
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
            //  set() 
            vm.$bus.$emit('message:push', '', 'success');
          } else {
            vm.$bus.$emit('message:push', response.data.message, 'danger');
          }
        });
    },
  },

  created() {
    const vm = this;
    vm.getProducts();
  },

  components: {
    Pagination,
  },
};
</script>
