<template>
  <div>
    <Loading :active.sync="isLoading" />
    <table class="table table-hover mt-4 bg-light shadow-sm rounded">
      <thead class="thead-dark">
        <tr>
          <th></th>
          <th>Email</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in orders" :key="key" v-if="orders.length" :class="{ 'text-secondary': !item.is_paid }">
          <td>{{ item.create_at | date }}</td>
          <td><span v-text="item.user.email" v-if="item.user"></span></td>
          <td>
            <ul class="list-unstyled">
              <li v-for="(product, i) in item.products" :key="i">
                {{ product.product.title }} {{ product.qty }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td>{{ item.id }}</td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td class="text-center">
            <strong v-if="item.is_paid" class="text-success"><i class="fa fa-check" aria-hidden="true"></i></strong>
            <span v-else class="text-danger"><i class="fa fa-times" aria-hidden="true"></i></span>
          </td>
          <td>
            <button class="btn btn-sm btn-outline-secondary" @click="openModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- pagination -->
    <Pagination :pagination="pagination" @gopage="getOrders"></Pagination>

    <BacktopBtn />

    <!-- Order Modal -->
    <div
      class="modal fade"
      id="orderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-4">
                <div class="form-group">
                  <label for="order_id"></label>
                  <input type="text" class="form-control" id="order_id" v-model="tempOrder.id" disabled />
                </div>
                <div class="form-group">
                  <label for="create_at"></label>
                  <input type="text" class="form-control" id="create_at" v-model="tempOrder.create_at" disabled />
                </div>
                <div class="form-group">
                  <label for="create_at"></label>
                  <input type="text" class="form-control" id="create_at" v-model="tempOrder.paid_date" disabled />
                </div>
                <div class="form-group form-check">
                  <input class="form-check-input" type="checkbox" value="" id="is_paid" v-model="tempOrder.is_paid" />
                  <label class="form-check-label" for="is_paid">
                    
                  </label>
                </div>
              </div>
              <div class="col-md-8">
                <!-- User Info -->
                <div class="form-group">
                  <label for="useremail">Email</label>
                  <input
                    type="email"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('email') }"
                    name="email"
                    id="useremail"
                    v-model="tempOrder.user.email"
                    v-validate="'required|email'"
                    placeholder=" Email"
                  />
                  <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
                </div>

                <div class="form-group">
                  <label for="username"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('name') }"
                    name="name"
                    id="username"
                    v-model="tempOrder.user.name"
                    v-validate="'required'"
                    placeholder=""
                  />
                  <span class="text-danger" v-if="errors.has('name')"></span>
                </div>

                <div class="form-group">
                  <label for="usertel"></label>
                  <input
                    type="tel"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('tel') }"
                    name="tel"
                    id="usertel"
                    v-model="tempOrder.user.tel"
                    v-validate="'required'"
                    placeholder=""
                  />
                  <span class="text-danger" v-if="errors.has('tel')"></span>
                </div>

                <div class="form-group">
                  <label for="useraddress"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{ 'is-invalid': errors.has('address') }"
                    name="address"
                    id="useraddress"
                    v-model="tempOrder.user.address"
                    v-validate="'required'"
                    placeholder=""
                  />
                  <span class="text-danger" v-if="errors.has('address')"></span>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-dark" data-dismiss="modal"></button>
            <button type="button" class="btn btn-secondary" @click="editOrder"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';
import BacktopBtn from '@/components/BacktopBtn';
import Alert from '@/components/AlertMessage';

export default {
  data() {
    return {
      orders: [],
      tempOrder: {
        products: [],
        user: {},
      },
      isLoading: false,
      pagination: {},
    };
  },

  methods: {
    openModal(item) {
      this.tempOrder = Object.assign({}, item); // item  tempOrder 
      $('#orderModal').modal('show');
      // console.log(this.tempOrder);
    },
    getOrders(page = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/orders?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
      });
    },
    editOrder() {
      const vm = this;
      const id = vm.tempOrder.id;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/order/${id}`;
      vm.isLoading = true;
      this.$http.put(api, { data: vm.tempOrder }).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        if (response.data.success) {
          vm.$bus.$emit('message:push', '', 'success');
          $('#orderModal').modal('hide');
          vm.getOrders();
        } else {
          vm.$bus.$emit('message:push', '', 'danger');
        }
      });
    },
  },
  created() {
    const vm = this;
    vm.getOrders();
  },
  components: {
    Pagination,
    BacktopBtn,
    Alert,
  },
};
</script>
